import Vue from 'vue';
import Vuex from 'vuex';
// import axios from 'axios';
Vue.use(Vuex)

const store = new Vuex.Store({
  modules: {},
  state: {
    userobj:''
  },
  mutations: {
    login(state, payload) {
     state.userobj = payload;
    },
    logout(state, payload) {
      state.userobj = '';
    }
  },
  actions: {

  }
})

export default store;